@import "../../../../css-config/mixins.scss";


.react-panel {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    z-index: 9999;
    


    .react-opac-layer {
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
       

    }

    .react-content {
        width: 100%;
        height: 100%;
        background: white;
        position: fixed;
        z-index: 1;
        display: flex;
        flex-direction: column;
       
        // transform: translateX(100%);
        

        @include md {
            width: calc(100% - 80%)

        }

       

        .react-logo2 {
            background-color: rgb(0, 0, 0);
            color: rgb(255, 255, 255);
            width: 100%;
            font-size: 20px;
            padding: 19px;
            position: fixed;
            bottom: 0;
            left: 0;
            z-index: 9;
            cursor: pointer;
           
           

           
    
            svg{
                color: red;
            }
        }



    }



    .react-study {
        width: 100%;
        height: 100%;
        background: rgb(255, 255, 255);
        position: fixed;
        left: 20%;
        z-index: 1;
        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        overflow-y: scroll;
        padding: 40px 60px;
       
       

        @include md {
            width: 80%

        }


       


       


        .react-title-heading{

            font-size: 34px;
            word-spacing: 6px;
            font-weight: 600;
            margin-top: 60px;

            &:after {
                content: "";
                display: block;
                margin-top: 5px;
                width: 50px;
                height: 3px;
                background-color: #8e2de2;
    
                @include md {
                    margin-top: 10px;
                }
            }


           
        }

        .react-title-desc{
            text-align: justify;
            font-size: 16px;
            line-height: 25px;           
            margin: 20px auto;
            word-spacing: 6px;
            // text-transform: capitalize;
            color: rgba(0, 0, 0, 0.8);

        }


        .react-image-intro{
            min-width: calc(100% - 20%);
            min-height: calc(100% - 30%);
            margin: 20px auto;
           
           
        }

        .react-title3-desc{
            text-align: justify;
            font-size: 16px;
            line-height: 25px;           
            margin: 20px auto;
            word-spacing: 6px;
            // text-transform: capitalize;
            color: rgba(0, 0, 0, 0.8);

        }

        .react-order-list{

            text-align: justify;
            font-size: 16px;
            line-height: 45px;           
            margin: 20px 20px;
            word-spacing: 6px;
            text-transform: capitalize;
            color: rgba(0, 0, 0, 0.8);

        }

        .react-question-answer{
            text-align: justify;
            font-size: 18px;
            line-height: 45px;           
            margin: 80px 0px;
            word-spacing: 6px;
            // text-transform: capitalize; 
            background-color: rgba(0, 0, 0, 0.87);
            color: rgb(255, 255, 255);
            padding: 20px;
            // font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
            letter-spacing: 3px;
            overflow: scroll;

        }

        
     


    }


   

    
   


}
