@import "../../../css-config/mixins.scss";

.project-main {
    margin: 40px 0;
    padding: 0 20px;


    .card-containar {
        display: grid;
        grid-template-columns: auto auto auto;
        gap: 10px;

        .card-start {
            position: relative;
            width: 50%;

            &:hover .overlay {
                height: 100%;
              }

            img {
                display: block;
                width: 100%;
                height: auto;
            }

            .overlay {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: #008CBA;
                overflow: hidden;
                width: 100%;
                height: 0;
                transition: .5s ease;
              }

              .overlay-contain {
                white-space: nowrap; 
                color: white;
                font-size: 20px;
                position: absolute;
                overflow: hidden;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
              }
        }
    }


}