@import '../../../../../../css-config/mixins.scss';

.fees{
 body {
      font-family: Arial, sans-serif;
      margin: 0;
      padding: 20px;
    }
    
    h1 {
      color: #333;
      text-align: center;
    }
    
    table {
      margin: 20px auto;
      border-collapse: collapse;
      width: 80%;

      @include cardmd{
        width: 50%;
      }
    }
    
    th, td {
      border: 1px solid #ddd;
      padding: 20px;
      text-align: left;
      @include cardmd{
        padding: 10px;
      }
    }
    
    th {
      background-color: #f5f5f5;
      font-weight: bold;
    }
    
    tr:nth-child(even) {
      background-color: #f9f9f9;
    }
    
    .note {
      color: #777;
      font-size: 14px;
      text-align: center;
    }
}

